import React, { useEffect, useState } from 'react';
import ExerciseImage from './../../../assets/images/exe.jpg';
import PlayBtn from './../../../assets/images/play-btn.png';
import PlayList from './../../../assets/images/play-list.png';

import ReactPlayer from 'react-player/lazy';
import './../../../container/Layout/Dashboard/detail.css';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { get_data } from './../../../redux/actions/product.js';
import { getAllExerciseslist, submitCompleteExercise } from '../../../apis/userApi';
import { errorSwal, succesSwal } from '../../../components/swal/Swal';

const DetailPage = ({ dataList }) => {
  let [toogle, setToogle] = useState(false);
  let [ImageToggle, setImageToggle] = useState(true);
  let [pressBtn, setPressBtn] = useState(dataList.difficulty);
  let [exerciseDetail, setExerciseDetail] = useState([]);
  let [titleName, setTitleName] = useState('');
  let [exerciseNum, setExerciseNum] = useState('');
  let [sideDesc, setSideDesc] = useState('');
  let [sideUrl, setSideUrl] = useState('');
  let [sidetrigger, setSideTrigger] = useState(false);
  let [sideKey, setSideKey] = useState('');
  let [sideId, setSideId] = useState('');
  let diffList = {easy:'This was easy', tough:'This was tough', okay:'This was okay'};

  const getAllExercises = async () => {
    // loading(true);
    let response = await getAllExerciseslist();
    if (response.status >= 400) {
      response = await response.json();
      this.props.loading(false);
      if (!response.message) {
        let message = '';
        for (const [key, value] of Object.entries(response)) {
          message = message + `${key} : ${JSON.stringify(value)} `;
        }
        errorSwal('oops', message);
      } else {
        errorSwal('oops', response.message);
      }
    } else if (response.status === 200) {
      response = await response.json();
      await setExerciseDetail(response.exercise_status);
    }
  };

  const playVideo = () => {
    setToogle(true);
    setImageToggle(false);
  };

  const completeExercise = async (type) => {
    var formdata = { is_completed: 'True', difficulty: type };

    let response = await submitCompleteExercise(sidetrigger ? sideId : dataList.id, formdata);
    if (response.status >= 400) {
      response = await response.json();
      this.props.loading(false);
      if (!response.message) {
        let message = '';
        for (const [key, value] of Object.entries(response)) {
          message = message + `${key} : ${JSON.stringify(value)} `;
        }
        errorSwal('oops', message);
      } else {
        errorSwal('oops', response.message);
      }
    } else if (response.status === 200) {
      response = await response.json();
      succesSwal(response.message.content);
    }
  };

  const pressButton = (type) => {
    setPressBtn(type);
    completeExercise(type);
  };

  useEffect(() => {
    getAllExercises();
  }, []);

  const playSideVideo = (data, key) => {
    setTitleName(data.series['title']);
    setExerciseNum(data.exercise['title']);
    setSideDesc(data.exercise['description']);
    setSideUrl(data.exercise['video']);
    setPressBtn(data.difficulty);
    setSideKey(data.exercise);
    setSideId(data.id);
    setSideTrigger(true);
  };


  return (
    
    <div>
      {Object.keys(dataList).length !== 0 ? (
        
        <div className="details_wrapper">
          <div className="video_detail">
            {ImageToggle ? (
              <div className="image__container">
                <img className="exe-img" alt="" src={ExerciseImage} />
                <div className="play-btn" onClick={() => playVideo()}>
                  <img src={PlayBtn} alt="" className="" width="50px" />
                </div>
              </div>
            ) : null}
            {toogle ? (
              <div className="video__container">
                <ReactPlayer
                  playing={true}
                  controls={true}
                  className="player_wrapper"
                  url={sidetrigger ? `${sideUrl}` : `${dataList.exercise['video']}`}
                />
              </div>
            ) : null}
            <div className="exercise__detail">
              <div className="e-d1">
                <div>
                  <h5>Week {sidetrigger ? sideKey.week_number : dataList.exercise.week_number} Day {sidetrigger ? sideKey.day_number : dataList.exercise.day_number}</h5>
                  <div>{sidetrigger ? exerciseNum : dataList.exercise['title']}: {sidetrigger ? titleName : dataList.series['title']}</div>
                </div>
                {/* <div onClick={() => !pressBtn ? pressButton(): ''} className={pressBtn ? 'press-btn' : 'check-btn'}>
                  <i
                    className={pressBtn ? `fa fa-check fa-checked` : `fa fa-check`}
                    aria-hidden="true"
                  ></i>
                  Mark as complete
                </div> */}
                 <div className="d-flex">
                  <div onClick={() => pressButton(diffList.easy)} className={"check-btn-fill orange-btn " + (pressBtn === diffList.easy ? 'orange-checked' : '')}>
                      <i className="fa fa-check"  ></i>
                      {diffList.easy}
                  </div>
                  <div onClick={() => pressButton(diffList.okay)} className={"check-btn-fill green-btn " + (pressBtn === diffList.okay ? 'green-checked' : '')}>
                      <i className="fa fa-check"  ></i>
                     {diffList.okay}
                  </div>
                  <div onClick={() => pressButton(diffList.tough)} className={"check-btn-fill red-btn " + (pressBtn === diffList.tough ? 'red-checked' : '')}>
                      <i className="fa fa-check"  ></i>
                      {diffList.tough}
                  </div>
                </div> 
              </div>
            </div>
            <div className="exercis__overview ">
              <div className="overview">Overview</div>
              <p>{sidetrigger ? sideDesc : 
                  dataList.exercise['description']} &nbsp;
                  {dataList.exercise.additional_resource ? dataList.exercise.additional_resource.map(value =>
                      <a href={value} key={value} rel="noopener noreferrer" target='_blank'>{value}, &nbsp;</a>
                    ) : ''
                  }
              </p>
            </div>
          </div>
          <div className="video_lists">
            {exerciseDetail.map((data, key) => (
              <div className="playlist__wrapper" key={key} onClick={() => playSideVideo(data, key)}>
                <div className="video__palylist">
                  <div>
                    <h6>Week {data.exercise.week_number} Day {data.exercise.day_number}</h6>
                    <p>{data.exercise.title}: {data.series['title']}</p>
                  </div>
                  <div onClick={() => playVideo()}>
                    <img src={PlayList} className="play-list-icon" alt="" />
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      ) : null}
    </div>
  );
};
const mapStateToProps = (state) => {
  return {
    dataList: state.app.listData,
  };
};
const mapDispatchToProps = (dispatch) => {
  return {
    get_data: (data, history) => dispatch(get_data(data, history)),
  };
};
export default withRouter(connect(mapStateToProps, mapDispatchToProps)(DetailPage));
