import React, { Component } from 'react';
import './style.css';

let steps = [{}, {}, {}, {}, {}, {},{}, {}, {}, {}, {}, {}];

class PageProgressBar extends Component {
  constructor(props) {
    super(props);

    this.state = {
      activeStep: this.props.data,
    };
  }

  componentDidMount = () => {
    document.getElementById('root').scrollIntoView();
    // this.setState({activeStep : this.props.data});
  };

  checkValidations = async () => {
    console.log('click on step', this.state.activeStep)
  };

  handleOnClickStepper = (step) => {
    this.setState({ activeStep: step });
  };

  makeStepper = (index) => {
      if(index < this.state.activeStep)
      {
        return(
             <div className="step done" key={index}>
              <div className="step-circle"><span>{index}</span></div>
              <div className="step-bar-left"></div>
              <div className="step-bar-right"></div>
            </div>
        )
      }
      else if(index === this.state.activeStep)
      {
        return(
             <div className="step editing" key={index}>
              <div className="step-circle"><span>{index === steps.length ? <i className="fas fa-trophy"></i> : index}</span></div>
              <div className="step-bar-left"></div>
              <div className="step-bar-right"></div>
            </div>
        )
      }
      else if(index === steps.length)
      {
        return(
             <div className="step" key={index}>
              <div className="step-circle"><span><i className="fas fa-trophy"></i></span></div>
              <div className="step-bar-left"></div>
              <div className="step-bar-right"></div>
            </div>
        )
      }
      else
      {
        return(
             <div className="step" key={index}>
              <div className="step-circle"><span>{index}</span></div>
              <div className="step-bar-left"></div>
              <div className="step-bar-right"></div>
            </div>
        )
      }

  }



  render() {
    return (
      <div className="stepper-horizontal">
        { steps.map(( value, index) => this.makeStepper(index+1))}
      </div>

    );
  }
}

export default PageProgressBar;
