/* eslint-disable jsx-a11y/alt-text */
import React, { useState, useEffect } from "react";
import "./../../../container/Layout/Dashboard/exercise.css";
import ExerciseImage from "./../../../assets/images/exe.jpg";
import PlayBtn from "./../../../assets/images/play-btn.png";
import {PageProgressBar} from "../../../components";

import ReactPlayer from "react-player/lazy";
import {
  submitCompleteExercise,
  getUserProfile,
  submitCompleteLesson,
} from "../../../apis/userApi";
import { errorSwal, succesSwal } from "../../../components/swal/Swal";

function Exercise() {
  const [userProfileData, setUserProfileData] = useState({});
  const [noData, setNoData] = useState(false);

  const [todaysId, setTodaysId] = useState();
  let [toogle, setToogle] = useState(false);
  let [ImageToggle, setImageToggle] = useState(true);
  let [isCompleted, setIsCompleted] = useState(false);
  let [difficulty, setDifficulty] = useState(null);
  let diffList = {easy:'This was easy', tough:'This was tough', okay:'This was okay'};

  const playVideo = () => {
    setToogle(true);
    setImageToggle(false);
  };

  const getUserProfileData = async () => {
    let response = await getUserProfile();
    if (response.status >= 400) 
    {
      response = await response.json();
      // this.props.loading(false);
      if (!response.message) {
        let message = "";
        for (const [key, value] of Object.entries(response)) {
          message = message + `${key} : ${JSON.stringify(value)} `;
        }
        errorSwal("oops", message);
      }
      else 
      {
        errorSwal("oops", response.message);
      }
    } 
    else if (response.status === 200) 
    {
      response = await response.json();
      let data = {};
      if (response.details.todays_exercise.length > 0) 
      {
        data = response.details.todays_exercise[0].exercise;
        data["type"] = "Todays Exercise";
        data["max_heart_rate"] = response.details.max_heart_rate;
        data["target_heart_rate"] = `${response.details.min_range_heart_rate} - ${response.details.max_range_heart_rate}` ;
        setTodaysId(response.details.todays_exercise[0].id);
        setDifficulty(response.details.todays_exercise[0].difficulty);
        setNoData(false);
        setUserProfileData(data);
      } 
      else if (response.details.todays_lesson.length > 0) 
      {
        data = response.details.todays_lesson[0].lesson;
        data["type"] = "Todays Lesson";
        data["max_heart_rate"] = response.details.max_heart_rate;
        data["target_heart_rate"] = `${response.details.min_range_heart_rate} - ${response.details.max_range_heart_rate}` ;
        
        setIsCompleted(response.details.todays_lesson[0].is_completed);
        setTodaysId(response.details.todays_lesson[0].id);
        setNoData(false);
        setUserProfileData(data);
      } 
      else setNoData(true);
    }
  };

  const completeLesson = async () => {
    var formdata = { is_completed: "True" };
    let response = null;
    
    response = await submitCompleteLesson(todaysId, formdata);
    if (response.status >= 400) {
      response = await response.json();
      // this.props.loading(false);
      if (!response.message) {
        let message = "";
        for (const [key, value] of Object.entries(response)) {
          message = message + `${key} : ${JSON.stringify(value)} `;
        }
        errorSwal("oops", message);
      } else {
        errorSwal("oops", response.message);
      }
    } else if (response.status === 200) {
      response = await response.json();
      succesSwal(response.message.content);
    }
  };

  const pressButton = () => {
    if (true) {
      setIsCompleted(true);
      completeLesson();
    }
  };

  const pressExerciseBtn = (type) => {
      setDifficulty(type);
      completeExercise(type);
  };

  const completeExercise = async (type) => {
    var formdata = { is_completed: "True", difficulty: type };
    let response = null;

    response = await submitCompleteExercise(todaysId, formdata);
    if (response.status >= 400) {
      response = await response.json();
      // this.props.loading(false);
      if (!response.message) {
        let message = "";
        for (const [key, value] of Object.entries(response)) {
          message = message + `${key} : ${JSON.stringify(value)} `;
        }
        errorSwal("oops", message);
      } else {
        errorSwal("oops", response.message);
      }
    } else if (response.status === 200) {
      response = await response.json();
      succesSwal(response.message.content);
    }
  };

  useEffect(() => {
    getUserProfileData();
  }, []);

  return (
    <div>
      
      {userProfileData.type && (
        <div className="exercise-wrapper">
          <PageProgressBar data={userProfileData.week_number} />
          {/* <p>{userProfileData.type}</p> */}
          {ImageToggle ? (
            <div className="image-container mt-3">
              <img className="exe-img" alt="" src={ExerciseImage} />
              <div className="play-btn" onClick={() => playVideo()}>
                <img src={PlayBtn} alt="" className="" width="50px" />
              </div>
            </div>
          ) : null}

          {toogle ? (
            <div className="image-container exe-img">
              <ReactPlayer
                playing={true}
                controls={true}
                className="player-wrapper"
                url={`${userProfileData.video}`}
              />
            </div>
          ) : null}

          <div className="exercise-detail">
            <div className="e-d1">
              <div>
                <h5>Week {userProfileData.week_number} Day {userProfileData.day_number}</h5>
                <div className="">{userProfileData.title}</div>
                <div className="text-muted pt-1">
                Maximum Heart Rate: {userProfileData.max_heart_rate} bpm
                </div>
                <div className="text-muted pb-3 pt-1">
                  Target Heart Rate: {userProfileData.target_heart_rate} bpm
                </div>
              </div>
              

              {userProfileData.type === 'Todays Exercise' ?
                <div className="d-flex">
                <div onClick={() => pressExerciseBtn(diffList.easy)} className={"check-btn-fill orange-btn " + (difficulty === diffList.easy ? 'orange-checked' : '')}>
                    <i className="fa fa-check"  ></i>
                    {diffList.easy}
                </div>
                <div onClick={() => pressExerciseBtn(diffList.okay)} className={"check-btn-fill green-btn " + (difficulty === diffList.okay ? 'green-checked' : '')}>
                    <i className="fa fa-check"  ></i>
                   {diffList.okay}
                </div>
                <div onClick={() => pressExerciseBtn(diffList.tough)} className={"check-btn-fill red-btn " + (difficulty === diffList.tough ? 'red-checked' : '')}>
                    <i className="fa fa-check"  ></i>
                    {diffList.tough}
                </div>
              </div> 
                :
                  <div
                  onClick={() => pressButton()}
                  className={isCompleted ? "press-btn" : "check-btn"}
                  >
                    <i
                      className={
                        isCompleted ? `fa fa-check fa-checked` : `fa fa-check`
                      }
                      aria-hidden="true"
                    ></i>
                    {isCompleted ? "Completed" : "Mark as complete"}
                </div>
              }
            </div>
          </div>
          <div className="exercis-overview mt-3">
            <div>Overview</div>
            <p>{userProfileData.description} &nbsp;
                {userProfileData.additional_resource ? userProfileData.additional_resource.map(value =>
                    <a href={value} key={value} rel="noopener noreferrer" target='_blank'>{value}, &nbsp;</a>
                  ) : ''
                }
            </p>
          </div>
        </div>
      )}

      {noData && (
        <div className="exercise-wrapper">
          <p className="text-center"> No Lesson/Exercise Available.</p>
        </div>
      )}
    </div>
  );
}

export default Exercise;
