import React, { useState } from 'react';
import './style.css';
import {TitleWrapper } from '../userInput/styles';

import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import getYear from 'date-fns/getYear';
import getMonth from 'date-fns/getMonth';

const MyDatePicker = (props) => {

  const [startDate, setStartDate] = useState(props.value);
  const years = Array.from(Array(new Date().getFullYear() - 1949), (_, i) => (i + 1950).toString());
  const months = [
    "Jan",
    "Feb",
    "Mar",
    "Apr",
    "May",
    "Jun",
    "Jul",
    "Aug",
    "Sep",
    "Oct",
    "Nov",
    "Dec",
  ];

  const onchangeDate = (date) => {
    setStartDate(date);
    props.onchange(date);
  }

  return (
    <>
    <TitleWrapper>
        <label>{props.label}</label>
        {props.error && <span className="error">{props.error}</span>}
    </TitleWrapper>
    <div className="d-flex align-items-center">
      <DatePicker
        renderCustomHeader={({
          date,
          changeYear,
          changeMonth,
          decreaseMonth,
          increaseMonth,
          prevMonthButtonDisabled,
          nextMonthButtonDisabled,
        }) => (
          <div
            style={{
              margin: 10,
              display: "flex",
              justifyContent: "center",
            }}
          >
            {/* <button type="button" onClick={decreaseMonth} disabled={prevMonthButtonDisabled}>
              {"<"}
            </button> */}
            <select
              value={getYear(date)}
              onChange={({ target: { value } }) => changeYear(value)}
            >
              {years.map((option) => (
                <option key={option} value={option}>
                  {option}
                </option>
              ))}
            </select>

            <select
              value={months[getMonth(date)]}
              onChange={({ target: { value } }) =>
                changeMonth(months.indexOf(value))
              }
            >
              {months.map((option) => (
                <option key={option} value={option}>
                  {option}
                </option>
              ))}
            </select>

            {/* <button type="button" onClick={increaseMonth} disabled={nextMonthButtonDisabled}>
              {">"}
            </button> */}
          </div>
        )}
        selected={startDate}
        onChange={(date) => onchangeDate(date)}
        placeholderText="Choose the date"
      />
      <i className="hide-show2 fas fa-calendar-alt" />
    </div>
    </>
  );
};

export default MyDatePicker;
