import React, { Component } from "react";
import { connect } from "react-redux";
import { withRouter, Link } from "react-router-dom";
import { Row, Col } from "react-bootstrap";
import {
  emailValidator,
  passwordValidator,
  confirmPasswordValidator,
  TextValidator,
} from "../../../shared/validators/Validators";
import { succesSwal, errorSwal } from "../../../components/swal/Swal";
import { UserInput, MyDatePicker } from "../../../components/";

import {
  setUserFirstName,
  authToken,
  authData,
} from "../../../redux/actions/authActions";
import { loading } from "../../../redux/actions/loaderActions";
import { signupUser } from "../../../apis/authApi";
import Header from "../../../container/Layout/Header";

import styles from "../sign-in/login-style.module.css";
import { SignupText, SignupButton } from "../../screens/LandingScreen/style";
import moment from "moment";


class SignUp extends Component {
  constructor(props) {
    super(props);

    this.state = {
      fullName: "",
      email: "",
      password: "",
      confirmPassword: "",
      dob: '',
      gender: "Male",
      show_hide: false,
      show_hide2: false,
    };
  }

  componentDidMount = async () => {
    const urlParams = new URLSearchParams(window.location.search);
    const referral_code = urlParams.get("referral_code");

    document.getElementById("root").scrollIntoView();
    await this.setState({
      referral_code,
    });
    this.checkToken();
  };

  checkToken = () => {
    if (localStorage.getItem("token")) {
      this.props.history.push("/initial-assesment");
    }
  };

  changeShowHide = () => {
    const hideShow = this.state.show_hide;
    if (hideShow) {
      this.setState({ show_hide: false });
    } else {
      this.setState({ show_hide: true });
    }
  };

  changeShowHide2 = () => {
    const hideShow = this.state.show_hide2;
    if (hideShow) {
      this.setState({ show_hide2: false });
    } else {
      this.setState({ show_hide2: true });
    }
  };

  handleChange = (event) => {
    this.setState({
      [event.target.name]: event.target.value,
    });
  };

  click = async (e) => {
    e.preventDefault();
    let errorStatus = false;

    let nameValidator = await TextValidator(this.state.fullName);
    let emailvalidator = await emailValidator(this.state.email);
    let passwordvalidator = await passwordValidator(this.state.password);
    let confirmPassValidator = await confirmPasswordValidator(
      this.state.password,
      this.state.confirmPassword
    );

    if (nameValidator) {
      this.setState({ nameError: nameValidator });
      errorStatus = true;
    }
    if (!this.state.dob) {
      this.setState({ dobError: 'Field cannot be empty.'});
      errorStatus = true;
    }
    if (emailvalidator) {
      this.setState({ emailError: emailvalidator });
      errorStatus = true;
    }
    if (passwordvalidator) {
      this.setState({ passwordError: passwordvalidator });
      errorStatus = true;
    }
    if (confirmPassValidator) {
      this.setState({ confirmPasswordError: confirmPassValidator });
      errorStatus = true;
    }

    if (!errorStatus) {
      this.submitSignup();
    }
  };

  submitSignup = async () => {
    this.props.loading(true);
    let response;
    let data = this.state.referral_code
      ? {
          email: this.state.email,
          password: this.state.password,
          name: this.state.fullName,
          referral_code: this.state.referral_code,
          dob: moment.utc(this.state.dob).local().format('YYYY-MM-DD'),
          gender: this.state.gender,
        }
      : {
          email: this.state.email,
          password: this.state.password,
          name: this.state.fullName,
          dob: moment.utc(this.state.dob).local().format('YYYY-MM-DD'),
          gender: this.state.gender
        };
    response = await signupUser(data);

    if (response.status >= 400) {
      response = await response.json();
      this.props.loading(false);
      if (!response.message) {
        let message = "";
        for (const [key, value] of Object.entries(response)) {
          message = message + `${key} : ${JSON.stringify(value)} `;
        }
        errorSwal("oops", message);
      } else {
        errorSwal("oops", response.message);
      }
    } else if (response.status === 200) {
      response = await response.json();

      this.props.loading(false);
      succesSwal(response.message);

      localStorage.setItem("token", response.token);
      localStorage.setItem("email", response.user.email);
      localStorage.setItem("firstName", response.user.name);
      localStorage.setItem("role", "client");

      this.props.setFirstName(response.user.name);
      this.props.setToken(response.token);

      this.props.history.push("/initial-assesment");
    }
  };

  render() {
    return (
      <div className="position-relative">
        <Header buttonText="signup" />
        <div
          className={`container ${styles.page_main}`}
          style={{ width: "100%" }}
        >
          <Row className="justify-content-center">
            <div className="col-lg-6 col-md-8 col-12">
              <SignupText isOk={true}>
                Sign up to <span style={{ color: "#eab105" }}>Exhale</span>
              </SignupText>
              <form className="mt-4" onSubmit={this.click}>
                <Row>
                  <Col xs={12} className="mt-2 mb-2">
                    <UserInput
                      label={"Full Name"}
                      error={this.state.nameError}
                      placeholder="Enter Your Full Name"
                      name="fullName"
                      value={this.state.fullName}
                      onChange={(e) => {
                        this.handleChange(e);
                        this.setState({ nameError: "" });
                      }}
                      maxLength="50"
                      required
                    />
                  </Col>
                  <Col xs={12} className="mt-2 mb-2">
                      <MyDatePicker 
                        value={''}
                        label={"Date of birth"}
                        error={this.state.dobError}
                        onchange={(date) => this.setState({ dob: date, dobError: '' })}></MyDatePicker>
                  </Col>

                  <Col xs={12} className="mt-2 mb-2">
                    <label className="mb-3">Gender</label>
                    <div onChange={e => {this.handleChange(e)}}>
                      <div className="d-flex">
                        <div className="d-flex align-items-baseline mr-5 pr-5">
                          <input
                            className="mr-3"
                            type="radio"
                            value="Male"
                            name="gender"
                            defaultChecked={this.state.gender === "Male"}
                          />
                          <p>Male</p>
                        </div>
                        <div className="d-flex align-items-baseline">
                          <input
                            className="mr-3"
                            type="radio"
                            value="Female"
                            name="gender"
                            defaultChecked={this.state.gender === "Female"}
                          />
                          <p>Female</p>
                        </div>
                      </div>
                      <div className="d-flex">
                        <div className="d-flex align-items-baseline mr-4 pr-4">
                          <input
                            className="mr-3"
                            type="radio"
                            value="Non-Binary"
                            name="gender"
                            defaultChecked={this.state.gender === "Non-Binary"}
                          />
                          <p>Non-Binary</p>
                        </div>
                        <div className="d-flex align-items-baseline ml-1">
                          <input
                            className="mr-3"
                            type="radio"
                            value="Do not wish to answer"
                            name="gender"
                            defaultChecked={this.state.gender === "Do not wish to answer"}
                          />
                          <p>Do not wish to answer</p>
                        </div>
                      </div>
                    </div>
                  </Col>
                 
                  <Col xs={12} className="mt-2 mb-2">
                    <UserInput
                      label={"Email"}
                      error={this.state.emailError}
                      placeholder="Enter your email"
                      name="email"
                      value={this.state.email}
                      onChange={(e) => {
                        this.handleChange(e);
                        this.setState({ emailError: "" });
                      }}
                      maxLength="60"
                      required
                    />
                  </Col>

                  <Col xs={12} className="mt-2 mb-2">
                    <UserInput
                      required
                      label={"Password"}
                      placeholder="Enter your Password"
                      name="password"
                      maxLength="30"
                      type={this.state.show_hide ? "text" : "password"}
                      error={this.state.passwordError}
                      value={this.state.password}
                      onChange={(e) => {
                        this.handleChange(e);
                        this.setState({ passwordError: "" });
                      }}
                      icon={
                        <i
                          className={
                            this.state.show_hide
                              ? "hide-show fa fa-eye"
                              : "hide-show fa fa-eye-slash"
                          }
                          onClick={() =>
                            this.setState({ show_hide: !this.state.show_hide })
                          }
                        />
                      }
                    />
                  </Col>

                  <Col xs={12} className="mt-2 mb-2">
                    <UserInput
                      required
                      label={"Confirm Password"}
                      type={this.state.show_hide2 ? "text" : "password"}
                      placeholder="Re-enter your password"
                      name="confirmPassword"
                      value={this.state.confirmPassword}
                      maxLength="30"
                      error={this.state.confirmPasswordError}
                      onChange={(e) => {
                        this.handleChange(e);
                        this.setState({ confirmPasswordError: "" });
                      }}
                      icon={
                        <i
                          className={
                            this.state.show_hide2
                              ? "hide-show fa fa-eye"
                              : "hide-show fa fa-eye-slash"
                          }
                          onClick={() =>
                            this.setState({
                              show_hide2: !this.state.show_hide2,
                            })
                          }
                        />
                      }
                    />
                  </Col>
                </Row>
                <Row className="justify-content-center">
                  <Col xs={12} className="mt-2">
                    <div className="my-3">
                      <input
                        type="checkbox"
                        value="lsRememberMe"
                        id="rememberMe"
                      />
                      <label
                        className={`ml-3 ${styles.remember_label}`}
                        htmlFor="rememberMe"
                      >
                        I agree with
                      </label>
                      <Link
                        className="pl-1"
                        from="/signup"
                        to="/termsandcondition"
                      >
                        Terms and Conditions
                      </Link>
                    </div>

                    <SignupButton
                      style={{ width: "100%" }}
                      type="submit"
                      className="mt-4"
                    >
                      Sign up
                    </SignupButton>
                    <Link from="/signup" to="/professional-signup">
                      <SignupButton outline={true} style={{ width: "100%" }}>
                        Sign up as professional
                      </SignupButton>
                    </Link>
                  </Col>
                </Row>
              </form>
            </div>
          </Row>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    loading: state.loader.loading,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    setToken: (token) => {
      dispatch(authToken(token));
    },
    setFirstName: (firstName) => {
      dispatch(setUserFirstName(firstName));
    },
    setLoginData: (loginData) => {
      dispatch(authData(loginData));
    },
    loading: (loadingStatus) => {
      dispatch(loading(loadingStatus));
    },
  };
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(SignUp));
